import React, { useState,useEffect,useCallback,FC } from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import { Routes } from '../Routes'
import { Link as RRDLink ,useHistory,useParams,Redirect } from 'react-router-dom';
import { observer, inject } from 'mobx-react';
import InputLabel from '@material-ui/core/InputLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import NativeSelect from '@material-ui/core/NativeSelect';
import filterParams from '../filter.params';
import Resizer from '../Components/resizer'
import Autocomplete from '@material-ui/lab/Autocomplete';
import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank';
import CheckBoxIcon from '@material-ui/icons/CheckBox';
import IconButton from '@material-ui/core/IconButton';
import PhotoCamera from '@material-ui/icons/PhotoCamera';
import HighlightOffIcon from '@material-ui/icons/HighlightOff';
import axios from "axios";
import Paper from '@material-ui/core/Paper';

import { ReactSortable, } from "react-sortablejs";
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';

import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';

import Divider from '@material-ui/core/Divider';

import CloseIcon from '@material-ui/icons/Close';


const CardList = ({list,setList}) => {

  
  return (
    <List style={{width:500}}>
    <Divider />
    <ReactSortable list={list} setList={setList}>
    {
      list.map((img,i)=>(
        <ListItem button key={img.url} >
          <ListItemText style={{'white-space': 'nowrap','text-overflow': 'ellipsis','overflow': 'hidden',}} primary={`Изображение ${i} - ${img.name}`} />
          <ListItemSecondaryAction>
              <IconButton onClick={()=>setList(list.filter((_,ind)=>i!==ind))} edge="end" aria-label="delete" style={{marginLeft:16}}>
                <CloseIcon />
              </IconButton>
    
          </ListItemSecondaryAction>
        </ListItem>
      ))
    }
    </ReactSortable>
    </List>
  );
}








const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  chb:{
    marginTop:16, 
    marginBottom:8
  },
  contentBox:{
    width:'100%',
    minHeight:300,
    display:'flex',
    justifyContent:'center',
    alignItems:'center'
  }

}));



const CreateMangaPage = observer(({authStore}) => {
  let { mangaId } = useParams();
  const classes = useStyles();
  const history = useHistory();
  const [fileList,setFileList] = useState([])
  const [chapter,setChapter] = useState(1)
  const [volume,setVolume] = useState(1)
  const [title,setTitle] = useState('')
  const uploadImg = async(file)=>{
    
    let formData = new FormData();
    formData.set('file', file);
    const res = await axios.post(`${process.env.REACT_APP_API_BASE}/upload/chapterImg`, formData)
    return res.data
  }
  const onFilesChange = async e => {
    if (e.target.files && e.target.files.length > 0) {
      const files = []
      for (const file of e.target.files) {
        const img = await uploadImg(file)
        files.push({...img,name:file.name})
        setFileList([...fileList,...files])
       
      }
    }
  }
  const submitChapter = async () => {
    const data = {imgList:fileList.map(f=>{
      const {url,w,h} = f
      return {url,w,h}
    }),chapter,volume,title,mangaId:parseInt(mangaId)}
    const res = await axios.post(`${process.env.REACT_APP_API_BASE}/chapter`, data)

    history.push(Routes.Manga.link(mangaId))
  }
  // useEffect(()=>setFileList(fileList.map((el,id)=>({...el,id}))),[fileList])
    if(!authStore.access_token){
    return <Redirect push to="/login" />
  }
  return (
    <>

      <div className={classes.paper}>
        <Typography component="h1" variant="h5">
          Добавление новой главы
        </Typography>
      <div>
        <TextField
            value={volume}
            onChange={e=>setVolume(e.target.value)}
            variant="outlined"
            margin="normal"
            id="volume"
            label='Том'
            name="volume"
            autoComplete="volume"
            autoFocus
          />
          <TextField
            value={chapter}
            onChange={e=>setChapter(e.target.value)}
            variant="outlined"
            margin="normal"
            id="chapter"
            label='Глава'
            name="chapter"
            autoComplete="chapter"
            autoFocus
          />
          <TextField
            value={title}
            onChange={e=>setTitle(e.target.value)}
            variant="outlined"
            margin="normal"
            id="title"
            label='Название'
            name="title"
            autoComplete="title"
            autoFocus
          />
</div>

<Paper square className={classes.contentBox}>
{  
  <div style={{display:'flex',flexDirection:'column',alignItems:'center'}}>
    {
      fileList.length===0&&
      <Typography variant="body2 caption" component="h3">
        Страниц пока нет
      </Typography>
    }
    <Typography variant="body2 caption" component="h3">
      {'Загрузить '}
      <input onChange={onFilesChange} accept="image/*" style={{display: 'none'}} id="icon-button-file" type="file" multiple/>
        <label htmlFor="icon-button-file">
          <Link>
            изображения
          </Link>
        </label>
    </Typography>
    <div style={{width:500}}>
      <CardList list={fileList} setList={(l)=>setFileList(l)}/>
    </div>
  </div>
}
</Paper>

          <br></br><br></br>
          <Button onClick={submitChapter} type="submit" style={{marginRight:8}} variant="outlined" size="medium" color="secondary">
            Добавить
          </Button>
          {/* <Button onClick={()=>history.push("/manga")}  variant="outlined" size="medium">
            Отмена
          </Button> */}

      </div>

    </>
  );
})





export default inject("authStore")(CreateMangaPage);







